import React from 'react';
import Lightbox from 'react-image-lightbox';
import Gallery1 from '../../images/gallery-1.jpg';
import Gallery2 from '../../images/gallery-2.jpg';
import Gallery3 from '../../images/gallery-3.jpg';
import Gallery4 from '../../images/gallery-4.jpg';
import Gallery5 from '../../images/gallery-5.jpg';
import Gallery6 from '../../images/gallery-6.jpg';
import Gallery7 from '../../images/gallery-7.jpg';
import Gallery8 from '../../images/gallery-8.jpg';

const images = [Gallery1, Gallery2, Gallery3, Gallery4, Gallery5, Gallery6, Gallery7, Gallery8];

class Images extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      photoIndex: 0,
      isOpen: false,
    }
  }
  render() {
    let GalleryImage = Gallery8;
    let imageList = this.props.image;
    if (imageList.image === "1") GalleryImage = Gallery1;
    else if (imageList.image === "2") GalleryImage = Gallery2;
    else if (imageList.image === "3") GalleryImage = Gallery3;
    else if (imageList.image === "4") GalleryImage = Gallery4;
    else if (imageList.image === "5") GalleryImage = Gallery5;
    else if (imageList.image === "6") GalleryImage = Gallery6;
    else if (imageList.image === "7") GalleryImage = Gallery7;
    else GalleryImage = Gallery8;
    let className = "col-md-3 col-12 join fedIn";
    const { photoIndex, isOpen } = this.state;
    return (
      <div className={className} style={{ padding: '0 0' }}>
        <img src={GalleryImage} style={{ width: '100%' }} className="img-fluid" alt="gallery images" style={{ borderRadius: '4px' }} />
        <div className="overlay" onClick={() => this.setState({ isOpen: true })}>
          <i className="far fa-eye overlay-icon"></i>
        </div>

        {
          isOpen && (
            <Lightbox 
            mainSrc={images[photoIndex]}
            nextSrc={images[(photoIndex + 1) % images.length]}
            prevSrc={images[(photoIndex + images.length - 1) % images.length]}
            onCloseRequest={() => this.setState({ isOpen: false })}
            onMovePrevRequest={() =>
              this.setState({
                photoIndex: (photoIndex + images.length - 1) % images.length,
              })
            }
            onMoveNextRequest={() =>
              this.setState({
                photoIndex: (photoIndex + 1) % images.length,
              })
            }/>
          )
        }
      </div>
    );
  }
}

export default Images;