import React from 'react';
import Images from './images';

class Gallery extends React.Component{
    constructor(){
        super();
        this.state={
            selected:'all'
        }
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(e){
        filterImage = [];
        filterImage1 = [];
        filterImage2 = [];
        this.setState({selected: e.target.value})
    }
    render(){
        const selected = this.state.selected;
        
        if(selected==="all"){
            IMAGES.forEach((imageList)=>{
                filterImage.push(
                    <Images image = {imageList} key={imageList.image}/>
                );
            });
        }else{
            filterImage = [];
            IMAGES.forEach((imageList)=>{
                if(imageList.category.indexOf(selected)===-1){
                    return;
                }
                filterImage.push(
                    <Images image = {imageList} key={imageList.image}/>
                );
            })
        }

        for(var i=0;i<filterImage.length;i++){
            if((i+2)%2===0){
                filterImage2.push(filterImage[i]);
            }else{
                filterImage1.push(filterImage[i]);
            }
        }

        if(filterImage1 < filterImage2){
            filterTemp = filterImage2;
            filterImage2 = filterImage1;
            filterImage1 = filterTemp;
        }
        return(
            <div className="gallery">
                {/* <div className="row justify-content-center" style={{margin: '0 0'}}>
                    <ul style={{padding: 0}}>
                        <li><button className={"gallery-menu " + (selected==="all" ? "active":"")} onClick={this.handleClick} value="all">All</button></li>
                        <li><button className={"gallery-menu " + (selected==="branding" ? "active":"")} onClick={this.handleClick} value="branding">branding</button></li>
                        <li><button className={"gallery-menu " + (selected==="web" ? "active":"")} onClick={this.handleClick} value="web">web</button></li>
                        <li><button className={"gallery-menu " + (selected==="logo design" ? "active":"")} onClick={this.handleClick} value="logo design">Logo Design</button></li>
                        <li><button className={"gallery-menu " + (selected==="photography" ? "active":"")} onClick={this.handleClick} value="photography">Photography</button></li>
                    </ul>
                </div>  */}
                <div className="gallery-image">
                    <div className="row fedIn" style={{margin: '0 0'}}>
                        {filterImage1}
                        {filterImage2}
                    </div>
                </div>     
            </div>
        );
    }
}

let filterImage = [];
let filterImage1 = [];
let filterImage2 = [];
let filterTemp = [];
const IMAGES = [
    {category:'branding',image: '1'},
    {category:'web',image: '2'},
    {category:'logo design',image: '3'},
    {category:'photography',image: '4'},
    {category:'branding',image: '5'},
    {category:'web',image: '6'},
    {category:'logo design',image: '7'},
    {category:'photography',image: '8'}
];
export default Gallery;